      const res = Number(reserve[1]) / 10 ** decimalOne;

      calPriceImpact = (amount / res) * 100;
      priceImpact =
        calPriceImpact - (calPriceImpact * LIQUIDITY_PROVIDER_FEE) / 100;
      if (isPriceImpact) {
        Number(priceImpact * 2);
      }
      setPriceImpact(priceImpact.toFixed(5));
